<!-- src/App.svelte -->
<script>
  import { fade } from 'svelte/transition';
  import Loader from './Modules/Loader.svelte';
  import IntroScreen from "./Modules/IntroScreen.svelte";
  import SetupScreen from "./Modules/SetupScreen.svelte";
  import GameScreen from "./Modules/GameScreen.svelte";
  import { onMount } from 'svelte';
  import AddPlayer from './Modules/AddPlayer.svelte';
  
  // Sample data: list of players with questions
  let debug = false;
  let debugPlayers = 3;
  let players = [];
  let introScreen = true;
  let setupScreen = false;
  let gameScreen = false;
  let player_colors = [
    "#696969", // Dim Gray
    "#8B4513", // Saddle Brown
    "#DC143C", // Crimson
    "#006400", // Dark Green
    "#008B8B", // Dark Cyan
    "#4B0082", // Indigo
    "#FF4500", // Orange Red
    "#C4A811", // Gold
    "#8B0000", // Dark Red
    "#4682B4", // Steel Blue
  ];
  
  // Function to handle adding a new question
  function handleAddPlayer(event) {
    if(player_colors.length === 0){
      alert("No more players allowed");
      return;
    }
    const newPlayer = event.detail;
    newPlayer.color = player_colors.pop();
    newPlayer.questions = [];
    newPlayer.vote = "skip";
    players = [...players, newPlayer];
  }

  function setupGame(){
    introScreen = false;
    setTimeout(() => {
      setupScreen = true;
    }, 500);
    gameScreen = false;
  }

  function startGame(){
    introScreen = false;
    setupScreen = false;
    setTimeout(() => {
      gameScreen = true;
    }, 500);
  }

  onMount(() => {
    //Quickly add players
    if(debug) return;
    const handleBeforeUnload = (event) => {
      if(debug) return false;
      if(players.length > 0){
        event.preventDefault();
        event.returnValue = '';
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload); //REVIEW: make this based on a debug variable

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload); //REVIEW: make this based on a debug variable
    };
  });
</script>

<style>
  .container {
    width: 360px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(0, 0, 0, 0.4);
    padding: 20px;
    max-width: 1100px;
    margin: 0 auto;
    color: white;
    z-index: 2;
    text-align: center;
    min-height: 100vh;
  }
  .bg-image {
    background-image: url(../assets/images/loaded.jpg);
    filter: blur(3px);
    -webkit-filter: blur(3px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    transform: scale(1.05);
  }

  h1 {
    font-size: 3rem;
    margin-bottom: 20px;
    text-align: center;
  }

  .debug {
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    opacity: .5;
  }
  .debug.active {
    opacity: 1;
  }

  @media screen and (max-width: 768px) {
    .debug { 
      display: none;
    }
    .container {
      width: 100%;
    }
  }
</style>

<div class="debug {debug ? 'active' : ''}">
  <button on:click={() => debug = !debug}>
    <i class="fas fa-bug"></i>
  </button>
</div>
<div class="bg-image"></div>
<Loader debug={debug} />
<div class="container">
  <h1>Loaded</h1>

  {#if introScreen}
  <div transition:fade={{duration: 500}}>
    <IntroScreen debug={debug} on:setupGame={setupGame} />
  </div>
  {/if}

  {#if setupScreen}
  <div transition:fade>
    <SetupScreen debug={debug} debugPlayers={debugPlayers} players={players} on:addPlayer={handleAddPlayer} on:startGame={startGame} />
  </div>
  {/if}

  {#if gameScreen}
  <div transition:fade>
    <GameScreen debug={debug} players={players} />
  </div>
  {/if}

</div>

