<!-- src/IntroScreen.svelte -->
<script>
  import { onMount } from "svelte";
  import Rules from "./Rules.svelte";
  import { slide } from "svelte/transition";
  import { createEventDispatcher } from "svelte";

  export let debug;
  let showRules = false;
  let active = false;
  const dispatch = createEventDispatcher();

  function toggleRules() {
    showRules = !showRules;
    active = !active;
  }

  function setupGame(){
    dispatch('setupGame');
  }
</script>

<style>
  #intro-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  p {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
</style>

<div id="intro-screen">
  <p>Welcome to Loaded, a game where by the participants try to get each other to debate topics or questions. The longest discussion wins. It's as simple as that (well, almost).</p>
  <button class="button {active ? 'active' : ''}" on:click={() => toggleRules()}>
    <i class="fas fa-book"></i> Read the Rules
  </button>
  {#if showRules}
  <div class="rules" transition:slide={{ duration: 800 }}>
    <Rules />
  </div>
  {/if}
  <button class="button" on:click={setupGame}>
    <i class="fas fa-play"></i> Setup the Game
  </button>
</div>