<script>
  export let players = [];
</script>

<style>
  .player {
    background-color: white;
    width: 50%;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
    padding: 10px;
    text-align: center;
    margin: 10px auto;
  }
</style>

<div>
  <h2>Players</h2>
  {#if players.length === 0}
    <p>No players yet.</p>
  {/if}
  {#each players as player}
    <div class="player" style="color: {player.color}">
      <i class="fas fa-user"></i>
      {player.name}
    </div>
  {/each}
</div>