<script>
  import { onDestroy, onMount } from "svelte";
  import { tick } from "svelte";
  import { run } from "svelte/internal";

  export let players = [];
  export let active_player;
  export let hide_vote_state = false;
  export let debug;
  export let run_timer = true;
  let speaker = "";
  let hours = 0;
  let minutes = 0;
  let seconds = 0;
  let miliseconds = 0;
  let timer = 0;
  let timerInterval;
  let second_multiplier = 0;

  function getCookie(name){
    let nameEQ = name + "=";
    let ca = document.cookie.split(';');
    for(let i=0;i < ca.length;i++) {
        let c = ca[i];
        while (c.charAt(0)==' ') c = c.substring(1,c.length);
        if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length,c.length);
    }
    return null;
  }

  function calculate_points(seconds_passed){
    second_multiplier = Math.floor(seconds_passed / 60);
    let speaker_points = 10;
    players.forEach((player, i) => {
      if(player.vote == "debate"){
        speaker_points += 1;
      }
    });
    players.forEach((player, i) => {
      let player_points = player.start_round_points;
      if(active_player.name == player.name){
        player_points += (speaker_points * second_multiplier);
      } else if(player.vote == "debate"){
        player_points += (5 * second_multiplier);
      }
      player.points = player_points;
    });
    orderPlayersByPoints();
    players = players;
  }

  function orderPlayersByPoints(){
    players = players.sort((a, b) => b.points - a.points);
    players = players;
  }

  onMount(() => {
    active_player.vote = "speaker";
    if(!run_timer) return;
    players.forEach(player => {
      if(player.points === undefined){
        player.points = 0;
      }
      player.start_round_points = player.points;
    })
    
    timerInterval = setInterval(() => {
      let dicussion_start_time = getCookie('discussion_start');
      timer = Date.now() - parseInt(dicussion_start_time);
      if(debug){
        timer += 59000 * (second_multiplier+1);
      }

      hours = Math.floor(timer / (1000 * 60 * 60));
      hours = (hours < 10 ? "0" + hours : hours);
      minutes = Math.floor((timer % (1000 * 60 * 60)) / (1000 * 60));
      minutes = (minutes < 10 ? "0" + minutes : minutes);
      seconds = Math.floor((timer % (1000 * 60)) / 1000);
      seconds = (seconds < 10 ? "0" + seconds : seconds);
      miliseconds = Math.floor((timer % 1000) / 100);
      miliseconds *= 10;
      miliseconds = (miliseconds < 10 ? "0" + miliseconds : miliseconds);

      let timer_in_seconds = parseInt(hours) * 3600 + parseInt(minutes) * 60 + parseInt(seconds);
      calculate_points(timer_in_seconds);
      tick();
    }, 100);
  });

  onDestroy(() => {
    clearInterval(timerInterval);
  });
</script>

<style>
  #timer {
    font-size: 2rem;
    margin-bottom: 20px;
  }

  .player-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }

  .player-list li {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 10px;
  }

  .player-list li i {
    margin-right: 10px;
  }

  .separator {
    margin: 0 5px;
  }
</style>

<!-- Do not display hours by default until it hits 1 -->
{#if parseInt(hours) === 0 && run_timer}
  <div id="timer">Timer: {minutes}:{seconds}:{miliseconds}</div>
{:else if parseInt(hours) >= 1 && run_timer}
  <div id="timer">Timer: {hours}:{minutes}:{seconds}:{miliseconds}</div>
{/if}
<ul class="player-list">
  {#each players as player}
    <li style="background-color: {player.color}">
      <i class="fas fa-user"></i>
      <span class="player-name">{player.name}{#if !hide_vote_state} ({player.vote}){/if}</span>
      <span class="separator"> - </span>
      <span class="points">Points: {player.points||0}</span>
    </li>
  {/each}
</ul>
