<script>
  import { onMount } from "svelte";
  // import { fade } from 'svelte/transition';
  import PlayerVoters from "./PlayerVoters.svelte";
  import Question from "./Question.svelte";
  import PlayerPoints from "./PlayerPoints.svelte";
  import { press } from 'svelte-gestures';

  export let players;
  export let debug;
  let active_index = 0;
  let round = 0;
  let debate_amount = 0;
  let winner = "";
  let active_player = players[0];
  let question = "";
  let show_question_input = false;
  let game_state = {
    select_player: true,
    view_question_button: false,
    view_question: false,
    voting: false,
    pre_discussion: false,
    discussing: false,
    round_complete: false,
  }
  let selecting_player_text = "Selecting speaker...";

  function deleteCookie(name) {
    document.cookie = name + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
  }

  function setCookie(name, value, expires_in_minutes) {
    //Convert expires to minutes for cookie
    let expires = ";expires=" + new Date(Date.now() + (expires_in_minutes * 60000)).toUTCString();
    document.cookie = name + "=" + value + ";" + expires;
  }

  function handleDebateAmount(){
    debate_amount = 0;
    players.forEach(player => {
      if(player.vote == "debate"){
        debate_amount += 1;
      }
    });
  }

  function handleChangeGameState(newState){
    game_state.select_player = false;
    game_state.view_question_button = false;
    game_state.view_question = false;
    game_state.voting = false;
    game_state.pre_discussion = false;
    game_state.discussing = false;
    game_state.round_complete = false;
    switch(newState){
      case "select_player":
        game_state.select_player = true;
      break;

      case "view_question_button":
        game_state.select_player = true;
        game_state.view_question_button = true;
      break;

      case "view_question":
        question = "";
        show_question_input = false;
        game_state.view_question = true;
      break;

      case "voting":
        players.forEach(player => {
          if(player.name != active_player.name){
            player.vote = "skip";
          }
        })
        game_state.voting = true;
      break;

      case "pre_discussion":
        game_state.pre_discussion = true;
      break;

      case "discussing":
        setCookie('discussion_start', Date.now(), (60*24*7));
        game_state.discussing = true;
      break;

      case 'end_discussion':
        active_player.questions.shift();
        active_player = nextPlayer(active_player);
        selecting_player_text = "Speaker is " + active_player.name;
        players.forEach(player => {
          player.vote = "skip";
        });
        debate_amount = 0;
        players = players;
        round += 1;

        if(round != players.length){
          game_state.select_player = true;
          game_state.view_question_button = true;
          return;
        }

        //Find the winner
        let max = 0;
        players.forEach(player => {
          if(player.points > max){
            max = player.points;
            winner = "Winner: " + player.name;
          }
        });
        let drawing = [];
        players.forEach(player => {
          if(player.points == max){
            drawing.push(player.name);
          }
        });

        if(drawing.length > 1){
          winner = "";
        }

        game_state.round_complete = true;
        round = 0;
      break;
    }
  }

  function addQuestion(){
    if(question === ""){ 
      alert("Please enter a question");
      return; 
    }
    show_question_input = false;
    active_player.questions = active_player.questions || [];
    active_player.questions.push(question);
    active_player = active_player;
    question = "";
    handleChangeGameState('voting');
  }

  function randomize() {
    handleChangeGameState('select_player');
    let count = 1;
    let timer = setInterval(() => {
      randomly_select_active_player();
      selecting_player_text = "Selecting speaker " + active_player.name;
      count += 1;
      if(debug || count > 15){
        clearInterval(timer);
        selecting_player_text = "Speaker is " + active_player.name;
        players.forEach((player, i) => {
          if(player.start_index === undefined){
            player.start_index = i;
          }
        });
        active_index = active_player.start_index;
        console.log(active_index);
        handleChangeGameState('view_question_button');
      }
    }, 200);
  }

  function randomly_select_active_player(){
    active_player = players[Math.floor(Math.random()*players.length)];
  }

  function nextPlayer(){
    let return_player;
    active_index = (active_index === players.length - 1) ? 0 : (active_index + 1);
    players.forEach((player, i) => {
      if(active_index === player.start_index){
        return_player = player;
      }
    });
    return return_player;
  }

  onMount(() => {
    randomize();
  })
</script>

<style>
  .flex {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  button {
    width: 100%;
  }
  .buttons button i {
    font-size: 1.5rem;
  }
  .card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
    padding: 10px;
    margin: 10px 0;
    min-width: 200px;
    max-width: 500px;
    text-align: center;
  }
  .card * {
    padding: 5px 0;
  }
  .card i {
    font-size: 50px;
  }
  .bigger{
    padding: 5px 20px 10px;
  }
  .bigger,
  .bigger i {
    font-size: 1.8rem;
  }

  .btn.red {
    background-color: rgb(142, 15, 15);
    color: white;
    border: none;
  }
  .btn.blue{
    background-color: rgb(15, 91, 142);
    color: white;
    border: none;
  }
  .btn.green{
    background-color: rgb(15, 142, 15);
    color: white;
    border: none;
  }
  .winner {
    font-size: 1.2rem;
    text-transform: uppercase;
    color: gold;
  }
  .question-textarea {
    width: 100%;
    min-height: 100px;
    padding: 10px;
  }
  .hold-animation {
    /* animation: hold 0.3s ease-in-out; */
    transition: all 0.5s ease-in-out;
    position: relative;
    overflow: hidden;
  }
  .hold-animation:after {
    content: "";
    background-color: #000;
    opacity: .5;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 0.5s ease-in-out;
    transform: scale(1.2) translateX(-100%);
  }
  .hold-animation:active {
    /* animation: hold 0.3s ease-in-out; */
    transform: scale(1.1);
  }
  .hold-animation:active:after {
    transform: scale(1.1) translateX(0%);
  }
  .card i.fa-trophy {
    font-size: 150px;
    color: gold;
  }
  table {
    width: 100%;
    margin-bottom: 20px;
    border-collapse:separate; 
    border-spacing: 0 1em;
  }
  table tbody td {
    padding: 10px;
  }
  .switch {
    position: relative;
    height: 26px;
    width: 100%;
    margin: 5px auto;
    background: rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    -webkit-box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
  }

  .switch-label {
    position: relative;
    z-index: 2;
    float: left;
    width: 50%;
    line-height: 26px;
    font-size: 18px;
    color: rgb(255, 255, 255);
    text-align: center;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 1);
    cursor: pointer;
  }
  .switch-label:active {
    font-weight: bold;
  }

  .switch-label-off {
    padding-left: 2px;
  }

  .switch-label-on {
    padding-right: 2px;
  }
  .switch-input {
    display: none;
  }
  .switch-input:checked + .switch-label {
    font-weight: bold;
    color: rgba(0, 0, 0, 0.65);
    text-shadow: 0 1px rgba(255, 255, 255, 0.25);
    transition: 0.15s ease-out;
  }
  .switch-input:checked + .switch-label-on ~ .switch-selection {
    left: 50%;
  }

  .switch-selection {
    position: absolute;
    z-index: 1;
    top: 2px;
    left: 2px;
    display: block;
    width: 49%;
    height: 22px;
    border-radius: 3px;
    background-color: #ffffff;
    box-shadow: inset 0 1px rgba(255, 255, 255, 0.5), 0 0 2px rgba(0, 0, 0, 0.2);
    transition: left 0.15s ease-out;
  }
</style>

<div class="flex">
  {#if game_state.select_player}
  <!-- <div transition:fade> --> <!-- REVIEW: make this based on a debug variable -->
  <div>
    <div class="card" style="background-color: {active_player.color}">
      <i class="fas fa-user"></i>
      <h2>{selecting_player_text}</h2>
    </div>
  </div>
  {/if}

  {#if game_state.view_question_button}
  <!-- <div transition:fade> --> <!-- REVIEW: make this based on a debug variable -->
  <div> 
    <section>
      <p>Pass the phone to this player</p>
    </section>
    <div>
      {#if !show_question_input && active_player.questions.length > 0}
      <button on:click={() => {show_question_input = true}} class="btn blue">
        <i class="fas fa-plus"></i>
        Add another question for next round
      </button>
      {/if}

      {#if show_question_input || active_player.questions.length === 0}
      <textarea type="text" bind:value={question} placeholder="Write an interesting question or topic..." class="question-textarea"></textarea>
      <button on:click={addQuestion} class="btn blue">
        <i class="fas fa-plus"></i>
        Lock in question
      </button>
      {/if}
    </div>
  </div>
  {/if}

  {#if game_state.voting}
  <!-- <div transition:fade> --> <!-- REVIEW: make this based on a debug variable -->
  <div>
    <section>
      <p>Discussion should not take place until after voting, otherwise you will not receive any points</p>
    </section>

    <section>
      <p>Read this aloud to the group</p>
    </section>

    <Question active_player={active_player} />

    <table>
      <thead>
        <tr>
          <th>Player</th>
          <th>Skip / Debate</th>
        </tr>
      </thead>
      <tbody>
        {#each [...players].sort((a, b) => a.start_index - b.start_index) as player, i}
        {#if player.name !== active_player.name}
        <tr style="margin-bottom: 10px;">
          <td style="background-color: {player.color}; color: white; border-radius: 10px;">
            <i class="fas fa-user"></i>
            <span class="player">{player.name}</span>
          </td>
          <td>
            <div class="switch">
              <input class="switch-input" type="radio" bind:group={player.vote} on:change={handleDebateAmount} value="skip" id="skip_{i}" />
              <label for="skip_{i}" class="switch-label switch-label-off">SKIP</label>
              <input class="switch-input" type="radio" bind:group={player.vote} on:change={handleDebateAmount} value="debate" id="debate_{i}" />
              <label for="debate_{i}" class="switch-label switch-label-on">DEBATE</label>
              <span class="switch-selection"></span>
            </div>
          </td>
        </tr>
        {/if}
        {/each}
      </tbody>
    </table>

    {#if debate_amount >= (players.length-1) / 2}
    <button on:click={() => {handleChangeGameState('discussing')}} class="btn green bigger">
      <i class="fas fa-balance-scale"></i>
      Begin discussion
    </button>
    {:else}
    <button on:click={() => {handleChangeGameState('end_discussion')}} class="btn green bigger">
      <i class="fas fa-play"></i>
      Next Player
    </button>
    {/if}
  </div>
  {/if}

  {#if game_state.discussing}
  <!-- <div transition:fade> --> <!-- REVIEW: make this based on a debug variable -->
  <div> 
    <section>
      Players who skipped are not allowed to debate
    </section>
    <Question active_player={active_player} />
    <PlayerPoints debug={debug} players={players} active_player={active_player} />
    <button on:click={() => {handleChangeGameState('end_discussion')}} class="btn red bigger">
      <i class="fas fa-comment-slash"></i>
      End discussion
    </button>
  </div>
  {/if}

  {#if game_state.round_complete}
  <!-- <div transition:fade> --> <!-- REVIEW: make this based on a debug variable -->
  <div> 
    <section>
      <p>Round complete</p>
    </section>
    <div class="card">
      <i class="fas fa-trophy"></i>
      <span class="player winner">{winner||"The round was a draw"}</span>
    </div>
    <PlayerPoints players={players} active_player={active_player} run_timer={false} hide_vote_state={true} />
    <section>
      <p>Press and hold the button for a new round</p>
    </section>
    <button use:press={{ timeframe: (debug ? 0 : 500), triggerBeforeFinished: false }} on:press={() => {randomize()}} class="btn green bigger hold-animation">
      <i class="fas fa-random"></i>
      Start next round
    </button>
  </div>
  {/if}
</div>