<!-- src/Modules/Loader.svelte -->
<script>
  import { fade } from 'svelte/transition';
  import { onMount } from 'svelte';

  export let debug;
  let visible = true;

  onMount(() => {
    if(debug){
      visible = false;
    } else {
      setTimeout(() => {
        visible = false;
      }, 1000);
    }
  });
</script>

<style>
  .loader {
    background-image: url(../assets/images/loaded.jpg);
    transform: scale(1.05);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    position: fixed;
    display: flex;
    height: 100%;
    width: 100%;
    /* background: white; */
    padding: 0;
    margin: 0;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    left: 0;
    top: 0;
    z-index: 3;
  }

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  h1 {
    background: rgba(0, 0, 0, .5);
    font-weight: bold;
    text-transform: uppercase;
    padding: 3px 10px 10px;
    margin: 0;
  }

  .spinner {
    background: rgba(0, 0, 0, 0.4);
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #db3434; /* Red */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
</style>

{#if visible}
<div class="loader" transition:fade={{ duration: 500 }}>
  <h1>Loaded is loading</h1>
  <div class="spinner"></div>
</div>
{/if}