<script>
  import AddPlayer from './AddPlayer.svelte';
  import PlayerList from './PlayerList.svelte';
  import { createEventDispatcher, onMount } from "svelte";
  
  const dispatch = createEventDispatcher();
  export let debug;
  export let debugPlayers;
  export let players;

  onMount(() => {
    if(debug){  
      for(let i = 0; i < debugPlayers; i++){
        dispatch('addPlayer', {name: 'Player ' + (i+1)});
      }
    }
  });

  function handleAddPlayer(event) {
    dispatch('addPlayer', event.detail);
  }

  function handleStartGame(event) {
    if(players.length > 1){
      dispatch('startGame');
    } else {
      alert("Please add at least 2 players.");
    }
  }
</script> 

<style>
  ul {
    list-style-type: none;
  }
  section {
    padding: 10px 20px 20px;
  }

  .start {
    margin-top: 10px;
  }
</style>

<section>
  <h1>Game Setup</h1>
  <ul>
    <li>Enter your name</li>
    <li>Enter your topic/question</li>
    <li>Press Add Player</li>
    <li>Pass the phone to the next player</li>
  </ul>
</section>
<section>
  <AddPlayer on:addPlayer={handleAddPlayer} />
</section>
<section>
  <PlayerList players={players} />
  <button class="start" on:click={handleStartGame}>
    <i class="fa-solid fa-play"></i>
    Start Game
  </button>
</section>