<script>
  import { createEventDispatcher } from "svelte";

  let name = "";
  let question = "";
  const dispatch = createEventDispatcher();

  function addPlayer() {
    if (name) {
      dispatch("addPlayer", { name });
      name = "";
    } else {
      alert("Please fill in the name.");
    }
  }
</script>

<style>
  .form-container {
    text-align: center;
  }
  input{
    display: block;
    padding: 10px;
    font-size: 1rem;
    width: 100%;
  }

  @media (max-width: 768px) {
    input {
      margin: 0 0 10px;
      width: 100%;
    }
  }
</style>

<div class="form-container">
  <h2>Add a player</h2>
  <input type="text" bind:value={name} placeholder="Enter player name..." />
  <button on:click={addPlayer}>
    <i class="fa-solid fa-plus" />
    Add Player
  </button>
</div>

